$arina: "arina", serif;
$mon: "Montserrat", sans-serif;
$color_black: #000;
$color_white: #fff;
$color_dark: #1e1e1e;
$color_light: #f8f8f8;
$color_purple: #a586ff;

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  font-family: $arina;
  color: $color_dark;
  background-color: $color_purple;
  min-width: 320px;

  &.lock {
    overflow: hidden;
  }

  &.black {
    background-color: $color_dark;

    .main {
      flex-grow: initial;
    }
  }
}

.container {
  max-width: 1920px;
  padding: 0 48px;
  margin: 0 auto;

  @include md {
    padding: 0 32px;
  }

  @include xs {
    padding: 0 14px;
  }
}

.title {
  text-align: center;
  font-family: $arina;
  font-weight: 400;
  font-size: 118px;
  // line-height: 78%;
  line-height: 1.2;
  text-transform: uppercase;

  span {
    display: block;

    &:nth-child(2) {
      transform: translateY(-35%);
    }

    &:nth-child(3) {
      transform: translateY(-70%);
    }
  }

  @media (max-width: 1439px) {
    font-size: 90px;
  }

  @include lg {
    font-size: 80px;
  }

  @include md {
    font-size: 70px;
  }

  @include sm {
    font-size: 45px;
  }
}

.num {
  width: 24px;
  margin: 0 auto;
  margin-bottom: 64px;

  span {
    display: block;
    width: 100%;
    height: auto;
  }

  @include lg {
    width: 19px;
    margin-bottom: 40px;
  }

  @include sm {
    width: 14px;
    margin-bottom: 30px;
  }
}

.cross {
  width: 44px;
  height: 44px;

  span {
    display: block;
    width: 100%;
    height: auto;
  }

  @include md {
    width: 26px;
    height: 26px;
  }

  @include sm {
    width: 20px;
    height: 20px;
  }
}
