.notFound {
  position: relative;
  padding: 230px 0;
  overflow: hidden;

  @include sm {
    padding: 100px 0 60px;
  }
}

.notFound__suptitle {
  font-family: $mon;
  margin-bottom: 33px;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-align: center;
  text-transform: uppercase;
  color: $color_light;
}

.notFound__title {
  position: relative;
  z-index: 5;
  margin-bottom: 48px;
  color: $color_light;
  line-height: 78%;

  @include sm {
    margin-bottom: 8px;
  }
}

.notFound__apply {
  text-decoration: none;
}
