.header-dark {
  &.sticky {
    background-color: $color_dark;
  }
}

.header-dark__wrapper {
  border-bottom: 2px solid $color_light;
}

.header-dark__disk {
  background: url("./../images/intro/intro-dark-disk.svg") top center / contain
    no-repeat;
}

.header-dark__menu,
.header-dark__login,
.header-dark__apply {
  color: $color_light;
}

.header-dark__menu {
  &.active {
    color: $color_light;
  }
}

.header-dark__apply {
  border: 1.7px solid $color_light;
}

// ================ HEADER NAVBAR ================

.header-dark__navbar {
  &.sticky {
    background-color: $color_dark;
  }
}

.header-dark__list {
  border-bottom: 2px solid $color_light;
}

.header-dark__link {
  color: $color_light;

  &:hover {
    color: #777;
  }
}
