.reasons {
  padding: 180px 0;
  overflow: hidden;

  & .container {
    position: relative;
  }

  @include md {
    padding: 80px 0 100px;
  }

  @include sm {
    margin: -30px 0;
    padding: 0;
    padding-bottom: 110px;
    background-color: $color_dark;
  }
}

.reasons__wrapper {
  position: relative;
  max-width: 1524px;
  margin: 0 auto;
  background-color: $color_dark;
  padding-top: 100px;
  overflow: hidden;

  @include xl {
    max-height: 743px;
  }

  @include lg {
    max-height: 610px;
  }

  @include md {
    max-height: 407px;
    padding-top: 40px;
  }

  @include sm {
    padding-top: 130px;
    max-height: none;
  }
}

.side-block-left,
.side-block-right {
  position: absolute;
  top: 0;
  width: 200px;
  height: 100%;
  border-radius: 51px;
  background-color: $color_purple;

  @include md {
    width: 123px;
    border-radius: 30px;
  }

  @include sm {
    display: none;
  }
}

.side-block-left {
  left: -100px;
}

.side-block-right {
  right: -100px;
}

.reasons__title {
  margin-bottom: 68px;
  color: $color_light;

  @include sm {
    margin-bottom: 40px;
  }
}

.reasons__container {
  max-height: 374px;
  padding-top: 20px;
  margin-top: -20px;
  overflow: hidden;

  @include md {
    margin: 0 auto;
    max-width: 540px;
  }

  @include sm {
    max-height: none;
  }
}

.reasons__item {
  text-align: center;
  color: $color_light;
  margin-bottom: 72px;
  opacity: 0.1;
  transition: all 0.2s linear;

  &.active {
    opacity: 1;
  }

  @include sm {
    opacity: 1;
    margin-bottom: 36px;
  }
}

.reasons__header {
  max-width: 500px;
  margin: 0 auto 12px;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 78%;
  text-transform: uppercase;

  @include lg {
    font-size: 45px;
  }

  @include md {
    max-width: 280px;
    font-size: 30px;
  }

  @include sm {
    margin-bottom: 6px;
  }
}

.reasons__text {
  max-width: 738px;
  margin: 0 auto;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;

  @include lg {
    font-size: 18px;
  }

  @include md {
    font-size: 14px;
  }
}
