.profile {
    padding-bottom: 72px;
}

.profile__title {
    margin-bottom: 16px;
}

.profile__control {
    position: relative;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0;

    @include md {
        justify-content: space-between;
    }

    @include sm {
        display: block;
    }
}

.profile__link {
    position: relative;
    font-family: $mon;
    font-weight: 400;
    font-size: 23px;
    line-height: 128%;
    text-transform: uppercase;
    text-decoration: none;
    color: #757575;
    transition: all 0.2s linear;

    &:not(:last-child) {
        margin-right: 52px;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 100%;
        background-color: $color_dark;
        height: 1px;
        opacity: 0;
        transition: all 0.2s linear;
    }

    &.active {
        color: $color_dark;

        &::after {
            opacity: 1;
        }
    }

    @media (max-width: 1099px) {
        font-size: 18px;
    }

    @include md {
        &:not(:last-child) {
            margin-right: 0;
        }
    }

    @include sm {
        display: block;
        width: fit-content;
        margin: 0 auto 15px;
        font-size: 16px;

        &:not(:last-child) {
            margin-right: auto;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.profile__wrapper {
    padding: 80px 0 120px;
    border-top: 1.7px solid #000;

    @include sm {
        padding: 40px 0 100px;
    }
}

.profile__header {
    grid-template-columns: 1fr;
    max-width: fit-content;
    margin: 0 auto 16px;
    padding: 0;
    text-align: center;
    border: none;
}

.profile__subheader {
    max-width: fit-content;
    margin: 0 auto 40px;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    text-align: center;
    color: $color_dark;

    @include sm {
        margin: 0 auto 20px;
    }
}

.profile__apply {
    appearance: none;
    outline: none;
    background-color: transparent;
    margin: 48px auto 0;
    text-align: center;
}

.profile__input,
.profile__select {
    display: block;
    width: 100%;
    max-width: 740px;
    margin: 0 auto 28px;
    border: none;
    border-bottom: 1.7px solid $color_dark;
    appearance: none;
    outline: none;
    background-color: transparent;
    padding: 23px 0 12px;

    &,
    &::placeholder {
        font-family: $mon;
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
        color: $color_dark;
        opacity: 1;
    }

    &::placeholder {
        opacity: 0.5;
    }

    @include sm {
        margin: 0 auto 16px;
        padding: 18px 0 8px;
        font-size: 18px;

        &::placeholder {
            font-size: 18px;
        }
    }

    @include xs {
        font-size: 16px;

        &::placeholder {
            font-size: 16px;
        }
    }
}

.profile__input-wrapper {
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 44px;

    @include sm {
        grid-template-columns: 1fr;
    }
}

#pass {
    margin-bottom: 8px;
}

.profile__help {
    max-width: 740px;
    margin: 0 auto;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_dark;
    opacity: 0.7;

    @include sm {
        font-size: 14px;
        line-height: 120%;
        text-align: center;
    }
}

#companyName {
    margin-bottom: 8px;

    & + .profile__help {
        margin-bottom: 20px;

        @include xs {
            margin-bottom: 5px;
        }
    }
}

.profile__works {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0 21px;

    @include md {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px 21px;
    }

    @include sm {
        grid-template-columns: 1fr;
    }
}

.profile__works-item {
    position: relative;

    &:hover {
        .profile__works-image img {
            transform: scale(1.05);
        }
    }
}

.profile__works-links {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    cursor: pointer;
}

.profile__works-image {
    margin-bottom: 30px;
    overflow: hidden;

    img {
        display: block;
        width: 100%;
        height: auto;
        transition: all 0.3s linear;
    }

    @include sm {
        margin-bottom: 10px;
    }
}

.profile__works-title {
    font-family: $mon;
    font-weight: 400;
    font-size: 23px;
    line-height: 128%;
    text-align: center;
    text-transform: uppercase;
    color: $color_dark;

    @include sm {
        font-size: 20px;
    }
}

.profile__works-category {
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    color: $color_dark;
    opacity: 0.5;

    @include sm {
        font-size: 18px;
    }
}

.profile__path {
    margin-top: 9px;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    text-align: center;
    color: rgba(30, 30, 30, 0.5);

    @include sm {
        margin-top: 0;
    }
}

.profile__accounts {
    padding-bottom: 20px;
    max-width: 100%;
    overflow: auto;
}

.profile__accounts-header,
.profile__accounts-item {
    min-width: 670px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 24px;
    border-bottom: 1.7px solid $color_black;

    @include sm {
        min-width: auto;
    }
}

.profile__accounts-header {
    @include sm {
        display: none;
    }
}

.profile__accounts-item {
    padding-top: 24px;

    @include sm {
        display: block;
    }
}

.accounts__title,
.accounts__item {
    &:nth-child(1) {
        width: 7.1%;
        margin-right: 0.7%;
    }
    &:nth-child(2) {
        width: 14.85%;
        margin-right: 0.7%;
    }
    &:nth-child(3) {
        width: 26.55%;
        margin-right: 0.7%;

        // @include md {
        //   max-width: 183px;
        // }
    }
    &:nth-child(4) {
        width: 16%;
        margin-right: 0.7%;
    }
    &:nth-child(5) {
        width: 16%;
        margin-right: 0.7%;
    }
    &:nth-child(6) {
        width: 16%;
    }
}

.accounts__title,
.accounts__item {
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_dark;

    @include lg {
        font-size: 16px;
    }

    @include md {
        font-size: 14px;
    }
}

.accounts__item {
    @media (max-width: 360px) {
        font-size: 12px;
    }
}

.accounts__item {
    @include sm {
        position: relative;
        padding-left: 52%;
        width: 100% !important;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &::before {
            text-align: right;
            width: 48%;
            position: absolute;
            top: 0;
            left: 0;
            font-family: $mon;
            font-weight: 400;
            font-size: 20px;
            line-height: 150%;
            color: $color_dark;
            opacity: 0.6;

            @include lg {
                font-size: 16px;
            }

            @include md {
                font-size: 14px;
            }

            @media (max-width: 360px) {
                font-size: 12px;
            }
        }
    }
}

@include sm {
    .account__id::before {
        content: "ID";
    }

    .account__data::before {
        content: "";
    }

    .account__name::before {
        content: " / ";
    }

    .account__status::before {
        content: "";
    }

    .account__price::before {
        content: "";
    }

    .account__link::before {
        content: "";
    }
}

.accounts__title {
    opacity: 0.6;
}

.account__link {
    a {
        color: inherit;
    }
}

.profile__select-label {
    display: block;
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_dark;
    opacity: 0.5;
}

.profile__select {
    padding-top: 0;
}

.profile__radio {
    width: 0;
    display: 0;
    opacity: 0;

    &:checked + .profile__input-label .radio__circle {
        display: block;
    }
}

.profile__input-label {
    position: relative;
    display: block;
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
    padding-left: 36px;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_dark;
    cursor: pointer;

    .radio__button {
        position: absolute;
        top: 50%;
        left: 0;
        width: 24px;
        height: 24px;
        z-index: 5;
        transform: translateY(-50%);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        border: 1.7px solid $color_dark;
    }

    .radio__circle {
        display: none;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: $color_dark;
    }

    @include xs {
        font-size: 18px;
    }
}

#projectLink {
    margin-bottom: 120px;

    @include sm {
        margin-bottom: 80px;
    }

    @include xs {
        margin-bottom: 40px;
    }
}

.profile__text {
    width: 100%;
    max-width: 646px;
    margin: 0 auto 52px;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_dark;
    text-align: center;
}

.profile__upload-area {
    position: relative;
    width: 100%;
    max-width: 740px;
    min-height: 136px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 12px;
    padding: 20px;
    border: 1.7px dashed $color_dark;

    &.active {
        border: 1.7px solid $color_light;
    }
}

.profile__upload-input {
    width: 0;
    display: 0;
    opacity: 0;
}

.profile__upload {
    text-align: center;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_dark;
    opacity: 0.5;

    // &.hidden {
    //   display: none;
    // }

    @include xs {
        font-size: 14px;
    }
}

.profile__upload-btn {
    text-decoration: underline;
    cursor: pointer;
}

.profile__upload-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    display: none;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 20px;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_dark;
    background-color: $color_purple;
    visibility: hidden;
    opacity: 0;

    @include md {
        font-size: 18px;
    }

    @include xs {
        font-size: 14px;
    }
}

.uploadError .profile__upload-list {
    z-index: 10;
    visibility: visible;
    opacity: 1;
    display: flex;
    color: $color_light;
}

.selected .profile__upload-list {
    z-index: 10;
    visibility: visible;
    opacity: 1;
    display: flex;
}

