// .profile__empty-close,
.profile__upload-close {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: -1;
    width: 19px;
    height: 18px;
    cursor: pointer;
}

.uploadError {
    & .profile__upload-close {
        visibility: visible;
        opacity: 1;
        z-index: 20;
    }
}

.profile__upload-sub {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 740px;
    margin: 0 auto 64px;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_dark;
    opacity: 0.7;

    &.profile__upload-2 {
        margin: 0 auto 80px;

        @include sm {
            .profile__upload-subtext {
                margin-bottom: 20px;
            }
        }
    }

    @include sm {
        display: block;
    }
}

.profile__request-link {
    text-decoration: underline;
    cursor: pointer;
    color: inherit;
}

.svg-image-profile-star {
    display: block;
    width: 32px;
    height: 32px;
    margin: 0 auto 16px;
}

.profile__note {
    width: 100%;
    max-width: 740px;
    margin: 0 auto 60px;
    text-align: center;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_dark;
}

// styles for validation

.profile__input.error__input {
    border-bottom: 1.7px solid $color_light;
}

.profile__input-item {
    position: relative;
    margin-bottom: 28px;

    .profile__input {
        margin-bottom: 0;
    }
}

.profile__input-block,
.profile__select-item {
    position: relative;
    width: 100%;
    max-width: 740px;
    margin: 0 auto;
}

.error {
    display: none;
    position: absolute;
    right: 0;
    bottom: -31px;
    text-align: right;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_light;

    &.show {
        display: block;
    }
}

.area-error {
    display: none;
    position: absolute;
    right: 10px;
    bottom: 5px;
    text-align: right;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    color: $color_light;
}

#companyName.error__input {
    margin-bottom: 30px;
}

// custom selects styles

.choices__inner {
    background-color: $color_purple;
    border: none;
    border-bottom: 1.7px solid $color_dark;
}

.is-focused .choices__inner,
.is-open {
    border-bottom: 1.7px solid $color_dark;
}

.choices__item--selectable {
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_dark;
}

.choices__list--dropdown .choices__item--selectable {
    color: $color_light;
}

.choices__list--single {
    padding: 0;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        top: 10px;
        right: 0;
        width: 18px;
        height: 10px;
        background: url("./../images/select-arrow.svg") center / contain
            no-repeat;
        transition: all 0.2s ease-in-out;
    }
}

.is-open .choices__list--single::after {
    transform: scaleY(-1);
}

.choices[data-type*="select-one"] .choices__inner {
    padding: 0;
}

// arrow
.choices[data-type*="select-one"]::after {
    // width: 18px;
    // height: 10px;
    border: none;
    // background: url("./../images/select-arrow.svg") center / contain no-repeat;
}

.choices__list--dropdown {
    padding: 12px 24px;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
    background-color: $color_dark;
    border: none;
    // color: $color_light;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
    background-color: $color_dark;
    border: none;
    color: $color_light;
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
    // position: relative;
    padding: 12px 0;
    font-size: 20px;
    line-height: 150%;

    @include xs {
        font-size: 14px;
        line-height: 120%;
    }
}

// .choices__list--dropdown .choices__item:not(:last-child) {
//   margin-bottom: 20px;
// }

.choices__item--selectable {
    &::before {
        display: none;
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 30px;
        height: 30px;
        background: url("./../images/list-star.svg") center / contain no-repeat;
    }
}

.choices__item--selectable.is-highlighted {
    &::before {
        display: block;
    }
}
