.about {
  overflow: hidden;
  // height: 100vh;
  // min-height: 558px;

  @include sm {
    height: auto;
  }
}

.about__wrapper {
  // height: 100vh;
  // min-height: 558px;
  border-top: 1.5px solid $color_dark;
  border-bottom: 1.5px solid $color_dark;
  display: grid;
  grid-template-columns: 1fr 2px 1fr;
  grid-template-rows: 1fr;

  @include lg {
    // min-height: 450px;
  }

  @include sm {
    display: block;
    height: auto;
  }
}

.about__block {
  &:first-child {
    align-self: center;
    text-align: center;
    transform: translateY(-14.29%);

    @include sm {
      transform: translateY(0);
      padding: 31px 0 50px;
      border-bottom: 1px solid $color_dark;
    }
  }

  &:last-child {
    overflow: hidden;
  }
}

.about__container {
  width: 90.96%;
  // padding: 256px 0 50px 48px;
  padding: 55px 0 50px 48px;

  @include md {
    width: 100%;
    // padding: 140px 0 50px 20px;
    padding: 21px 0 30px 20px;
  }

  @include sm {
    padding: 23px 0 30px 0;
    text-align: center;
  }
}

.about__num {
  margin: 0 auto 40px;

  @include sm {
    margin: 0 auto 30px;
  }
}

.about__title {
  margin-bottom: 20px;
  color: $color_dark;
}

.about__more {
  padding: 18px 42px;
  border-radius: 50%;
  border: 1.4px solid $color_dark;
  appearance: none;
  outline: none;
  background-color: transparent;
  font-family: $mon;
  font-weight: 400;
  font-size: 18px;
  line-height: 128%;
  text-align: center;
  color: $color_dark;
  cursor: pointer;

  @include sm {
    padding: 17px 40px;
    font-size: 16px;
  }
}

.about__line {
  height: 100%;
  width: 2px;
  background-color: $color_dark;

  @include sm {
    display: none;
  }
}

.about__msg {
  margin-bottom: 8px;
  font-family: $arina;
  font-weight: 400;
  font-size: 50px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_dark;

  @include lg {
    font-size: 38px;
  }

  @include md {
    font-size: 30px;
  }

  @include sm {
    font-size: 24px;
  }
}

.about__text {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  color: $color_dark;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  @include md {
    font-size: 14px;
  }
}

.categories__tab {
  animation: fadeEffect 1s;
}

@keyframes fadeEffect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
