.categories-special {
  position: relative;
  overflow: hidden;
  padding: 48px 0 130px;
  // background-color: $color-dark;

  @include md {
    padding: 40px 0 80px;
  }

  @include sm {
    padding: 20px 0 120px;
  }
}

.categories-special__container {
  max-width: 1432px;
  margin: 0 auto;
  padding: 0 20px;
  color: $color_light;
}

.categories-special__title {
  max-width: 731px;
  margin: 0 auto 17px;

  @include md {
    margin-bottom: 14px;
  }

  @include sm {
    margin-bottom: 8px;
  }
}

.categories-special__text {
  max-width: 919px;
  margin: 0 auto 80px;
  font-family: $mon;
  font-weight: 400;
  font-size: 24px;
  line-height: 132%;
  text-align: center;
  color: $color_light;

  @include lg {
    font-size: 20px;
  }

  @include md {
    margin-bottom: 40px;
    padding: 0 32px;
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;
  }

  @include xs {
    padding: 0 14px;
  }
}

.categories-special__tab {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0 auto;

  @include lg {
    max-width: 900px;
  }

  @include md {
    display: block;
  }
}

.categories-special__item {
  display: flex;
  min-width: fit-content;
  align-items: center;

  @include md {
    display: block;
  }

  &:hover .categories-special__link {
    opacity: 1;
  }

  &.non-active {
    opacity: 0.4;
  }
}

.categories-special__star {
  margin: 0 28px;

  &.non-active {
    opacity: 0.4;
  }

  @include md {
    margin: 0 auto;
  }
}

#special-item-1,
#special-item-3 {
  .categories-special__star {
    display: none;
  }
}

#special-item-1,
#special-item-2 {
  margin-bottom: 16px;
}

@include md {
  #special-item-3 {
    .categories-special__star {
      display: block;
    }
  }

  #special-item-1,
  #special-item-2,
  #special-item-3 {
    margin-bottom: 18px;
  }

  .categories-special__star {
    margin-bottom: 18px;
  }
}

.categories-intro__wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
}

.categories-special__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.categories-special__info {
  display: flex;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  text-decoration: none;
  color: $color_light;
  transition: all 0.2s linear;
  text-align: center;

  @include lg {
    font-size: 40px;
  }

  @include sm {
    font-size: 30px;
  }

  @include xs {
    display: inline-block;
    max-width: 360px;
    font-size: 28px;
  }
}

.categories-special__icon {
  & img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include lg {
    width: 15px;
  }

  @include sm {
    width: 10px;
  }
}
