.topics {
    overflow: hidden;
    height: 100vh;
    min-height: 558px;

    @include sm {
        position: relative;
        z-index: 2;
        height: auto;
        padding-bottom: 74px;
        border-bottom-left-radius: 30px;
        border-bottom-right-radius: 30px;
        background-color: $color_purple;
    }
}

.topics__wrapper {
    height: 100vh;
    min-height: 558px;
    border-top: 1.5px solid $color_dark;
    border-bottom: 1.5px solid $color_dark;
    display: grid;
    grid-template-columns: 1fr 2px 1fr;
    grid-template-rows: 1fr;

    @include sm {
        display: block;
        height: auto;
    }
}

.topics__block {
    &:first-child {
        align-self: center;
        text-align: center;
        // transform: translateY(-14.29%);

        @include sm {
            transform: translateY(0);
            padding: 31px 0 50px;
            border-bottom: 1px solid $color_dark;
        }
    }

    &:last-child {
        overflow: hidden;
    }
}

.topics__container {
    width: 90.96%;
    padding: 56px 0 50px 48px;

    @include md {
        width: 100%;
        padding: 50px 0 50px 20px;
    }

    @include sm {
        max-width: 520px;
        margin: 0 auto;
        padding: 23px 0 30px 0;
        text-align: center;
    }
}

.topics__num {
    margin: 0 auto 40px;

    @include sm {
        margin: 0 auto 30px;
    }
}

.topics__title {
    max-width: 500px;
    margin: 0 auto 20px;
    color: $color_dark;
}

.topics__more {
    padding: 18px 42px;
    border-radius: 50%;
    border: 1.4px solid $color_dark;
    appearance: none;
    outline: none;
    background-color: transparent;
    font-family: $mon;
    font-weight: 400;
    font-size: 18px;
    line-height: 128%;
    text-align: center;
    color: $color_dark;
    cursor: pointer;

    @include sm {
        padding: 17px 40px;
        font-size: 16px;
    }
}

.topics__line {
    height: 100%;
    width: 2px;
    background-color: $color_dark;

    @include sm {
        display: none;
    }
}

.topics__msg {
    margin-bottom: 12px;
    font-family: $arina;
    font-weight: 400;
    font-size: 50px;
    line-height: 128%;
    text-transform: uppercase;
    color: $color_dark;

    @include lg {
        font-size: 38px;
    }

    @include md {
        margin-bottom: 6px;
        font-size: 30px;
    }

    @include sm {
        margin-bottom: 0;
        font-size: 24px;
    }
}

.topics__text {
    margin-bottom: 72px;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 136%;
    color: $color_dark;

    &:last-child {
        margin-bottom: 0;
    }

    @include md {
        margin-bottom: 36px;
        font-size: 14px;
    }

    @include sm {
        margin-bottom: 28px;
    }
}

.categories__tab {
    animation: fadeEffect 1s;
}

@keyframes fadeEffect {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
