.footer {
  margin-top: -10px;
  padding: 130px 0 101px;
  background-color: $color_dark;

  .container {
    position: relative;
  }

  @include md {
    padding: 47px 0 60px;
  }

  @include sm {
    padding: 61px 0 40px;
  }
}

.footer__line {
  position: absolute;
  top: -120px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 96px);
  height: 2px;
  background-color: $color_light;

  @include sm {
    display: none;
  }
}

// For black pages
.black {
  .footer__line {
    @include md {
      top: -47px;
    }

    @include sm {
      top: -61px;
      display: block;
      width: calc(100% - 28px);
    }
  }
}
// End for black pages

// For footer without line
.without_line {
  .footer {
    margin-top: 0;
    box-shadow: 0px -37px 138px #f9f8ef;
  }

  .footer__line {
    display: none;
  }

  &.no_shadow .footer {
    box-shadow: none;
  }
}
// End for footer without line

.footer__title {
  max-width: 630px;
  margin: 0 auto 60px;
  color: $color_light;

  @media (max-width: 1439px) {
    max-width: 500px;
  }

  @include md {
    max-width: 410px;
    margin: 0 auto 28px;
  }

  @include sm {
    max-width: 260px;
    margin: 0 auto 40px;
  }
}

.footer__form {
  max-width: 740px;
  margin: 0 auto 40px;

  @include md {
    max-width: 400px;
    margin: 0 auto 28px;
  }
}

.footer__input {
  display: block;
  width: 100%;
  height: 42px;
  margin-bottom: 40px;
  border: none;
  border-bottom: 1.7px solid $color_light;
  appearance: none;
  outline: none;
  background-color: transparent;
  text-align: center;
  text-align: center;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: $color_light;

  &::placeholder {
    text-align: center;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: $color_light;
    opacity: 0.4;
  }

  @include md {
    font-size: 14px;

    &::placeholder {
      font-size: 14px;
    }
  }
}

.footer__submin {
  display: block;
  margin: 0 auto;
  width: 205px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  border: 1.7px solid $color_light;
  background-color: transparent;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_light;

  @include md {
    width: 192px;
    height: 60px;
    font-size: 18px;
  }

  @include sm {
    width: 176px;
    height: 55px;
    font-size: 16px;
  }
}

.footer__text {
  margin-bottom: 24px;
  text-align: center;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;
  color: $color_light;
}

.footer__additional {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include sm {
    display: block;
    text-align: center;
  }
}

.footer__copyright,
.footer__design {
  font-family: $mon;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: $color_light;

  & a {
    color: inherit;
  }

  @include md {
    max-width: 105px;
    font-size: 14px;
  }

  @include sm {
    max-width: none;
  }
}

.footer__copyright {
  @include sm {
    margin-bottom: 6px;
  }
}

.footer__social {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;

  @include sm {
    position: static;
    transform: translateX(0);
    margin-bottom: 70px;
  }
}

.footer__item {
  display: inline-block;
  width: 40px;
  height: 40px;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 24px;
  }

  & span {
    display: inline-block;
  }

  @include md {
    &:not(:last-child) {
      margin-right: 19px;
    }
  }
}
