.menu {
  display: none;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 100000;
  width: 100%;
  min-height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: $color_dark;
  transition: all 0.3s ease-in-out;

  @include md {
    display: block;
  }

  &.active {
    transform: translateX(100%);
  }
}

.menu__disk {
  position: absolute;
  top: -292px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  width: 100%;

  @include xs {
    width: 130%;
    top: -240px;
  }

  &::after {
    content: "";
    display: block;
    padding-top: 100%;
  }

  .menu__disk-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("./../images/menu-disk.svg") center / contain no-repeat;
  }
}

.menu__header {
  height: 75px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1.5px solid $color_light;

  @include xs {
    height: 55px;
  }
}

.menu__login {
  width: 39px;
  height: 39px;

  span {
    display: block;
    width: 100%;
    height: auto;
  }

  @include xs {
    width: 25px;
    height: 25px;
  }
}

.menu__close {
  width: 27px;
  height: 29px;
  cursor: pointer;

  span {
    display: block;
    width: 100%;
    height: auto;
  }

  @include xs {
    width: 20px;
    height: 19px;
  }
}

.menu__main {
  padding: 35px 0 54px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.menu__logo {
  width: 100%;
  margin-bottom: 70px;

  img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include xs {
    margin-bottom: 24px;
  }
}

.menu__wrapper {
  width: 100%;
  height: calc(100vh - 75px - 35px - 155px - 70px - 54px);
  max-height: 628px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.menu__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  @include xs {
    display: block;
    max-width: 120px;
    margin: 0 auto 40px;
  }
}

.menu__item {
  @include xs {
    text-align: center;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.menu__link {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_light;
  text-decoration: none;

  @include sm {
    font-size: 18px;
  }

  @include xs {
    font-size: 16px;
  }
}

.menu__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.menu__cross {
  margin-bottom: 40px;

  @include xs {
    margin-bottom: 10px;
  }
}

.menu__title {
  margin-bottom: 31px;
  color: $color_light;

  @include xs {
    margin-bottom: 8px;
    font-size: 30px;
    line-height: 128%;
  }
}

.menu__apply {
  width: 192px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1.4px solid $color_light;
  font-family: $mon;
  font-weight: 400;
  font-size: 18px;
  line-height: 128%;
  text-align: center;
  color: $color_light;
  cursor: pointer;

  @include xs {
    width: 176px;
    height: 55px;
    font-size: 16px;
  }
}

.menu__footer {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;

  @include xs {
    justify-content: center;
  }
}

.menu__copyright,
.menu__design,
.menu__design a {
  font-family: $mon;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  color: $color_light;

  @include xs {
    display: none;
  }
}

.menu__social {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;

  @include xs {
    position: static;
    transform: translateX(0);
  }
}

.social__item {
  display: inline-block;
  width: 40px;
  height: 40px;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 24px;
  }

  & span {
    display: inline-block;
  }

  @include xs {
    width: 30px;
    height: 30px;
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
