.speakers {
  position: relative;
  padding-top: 172px;
  margin-top: -5px;

  @include xl {
    padding-top: 128px;
  }

  @include md {
    padding-top: 60px;
  }

  @include sm {
    padding-top: 30px;
  }
}

.speakers__triangle-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 195px solid $color_dark;
  border-left: 195px solid $color_dark;
  border-right: 195px solid transparent;
  border-bottom: 195px solid transparent;

  @include lg {
    border-top: 100px solid $color_dark;
    border-left: 100px solid $color_dark;
    border-right: 100px solid transparent;
    border-bottom: 100px solid transparent;
  }

  @include sm {
    border-top: 50px solid $color_dark;
    border-left: 50px solid $color_dark;
    border-right: 50px solid transparent;
    border-bottom: 50px solid transparent;
  }
}

.speakers__triangle-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: 195px solid $color_dark;
  border-right: 195px solid $color_dark;
  border-left: 195px solid transparent;
  border-bottom: 195px solid transparent;

  @include lg {
    border-top: 100px solid $color_dark;
    border-right: 100px solid $color_dark;
    border-left: 100px solid transparent;
    border-bottom: 100px solid transparent;
  }

  @include sm {
    border-top: 50px solid $color_dark;
    border-right: 50px solid $color_dark;
    border-left: 50px solid transparent;
    border-bottom: 50px solid transparent;
  }
}

.speakers__container {
  position: relative;
  z-index: 2;
  color: $color_light;
}

.speakers__title {
  max-width: fit-content;
  margin: 0 auto;
  position: relative;
  margin-bottom: 20px;
  color: $color_dark;

  & span {
    position: absolute;
    top: 0;
    right: -80px;
  }
}

.speakers__text {
  max-width: 919px;
  margin: 0 auto 76px;
  font-family: $mon;
  font-weight: 400;
  font-size: 24px;
  line-height: 132%;
  text-align: center;
  color: $color_dark;

  @include lg {
    font-size: 20px;
  }

  @include md {
    margin-bottom: 28px;
    padding: 0 32px;
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;
  }

  @include xs {
    padding: 0 14px;
  }
}

.speakers__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  padding: 0 40px;

  @include md {
    margin-bottom: 53px;
  }

  @include sm {
    margin-bottom: 40px;
  }

  @include xs {
    display: none;
  }
}

.speakers__prev,
.speakers__next {
  & span {
    display: inline-block;
    cursor: pointer;
  }
}

.speakers__slider {
  width: 100%;
  overflow: hidden;
}

.speakers__item {
  position: relative;

  &:after {
    content: "";
    display: block;
    padding-top: 80.88%;

    @include xl {
      padding-top: 88.88%;
    }

    @media (max-width: 370px) {
      padding-top: 95%;
    }
  }
}

.speakers__item-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  z-index: 2;
}

.speakers__image-wrapper {
  position: relative;
  height: 76.97%;
  margin-bottom: 40px;
  overflow: hidden;

  @include xl {
    margin-bottom: 30px;
  }

  @include lg {
    margin-bottom: 20px;
  }

  @include md {
    height: 76%;
    margin-bottom: 10px;
  }
}

.speakers__disk {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 157.25%;
  height: 252.61%;
  border-radius: 50%;
  overflow: hidden;
}

.disk__shadow {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 100;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: inset 0px 0px 108px $color_white;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;

  @include sm {
    display: none;
  }
}

.speakers__img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 63.59%;
  height: 39.59%;

  & img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include xl {
    height: 37.2%;
  }

  .speakers__descr {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 28px 61px 0;
    box-shadow: inset 0px 0px 108px $color_white;
    background-color: $color_purple;
    text-align: center;
    font-family: $mon;
    font-weight: 400;
    font-size: 24px;
    line-height: 132%;
    color: $color_dark;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease-in-out;

    @include xl {
      font-size: 20px;
    }

    @media (max-width: 1400px) {
      font-size: 14px;
      padding: 32px 25px 0;
    }

    @include md {
      font-size: 12px;
    }

    @include sm {
      display: none;
    }
  }
}

.speakers__item:hover .speakers__descr,
.speakers__item:hover .disk__shadow {
  opacity: 1;
  visibility: visible;
}

.speakers__name,
.speakers__function {
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 78%;
  text-align: center;
  text-transform: uppercase;
  color: $color_dark;

  @include lg {
    font-size: 45px;
  }

  @include md {
    font-size: 30px;
  }

  @include sm {
    font-size: 24px;
  }

  @media (max-width: 370px) {
    font-size: 20px;
  }
}
