.categories {
  position: relative;
  overflow: hidden;
  margin-bottom: -10px;
}

.categories__bg {
  position: absolute;
  top: 143px;
  left: 50%;
  transform: translateX(-50%);
  width: 103.8%;

  &:after {
    content: "";
    display: block;
    padding-top: 100%;
  }

  @include md {
    top: 80px;
  }

  @include md {
    top: 39px;
  }
}

.categories__disk {
  position: absolute;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: $color-dark;
  box-shadow: 0px -37px 184px #f9f8ef;

  @include sm {
    box-shadow: 0px -15px 100px #f9f8ef;
  }
}

.categories__wrapper {
  position: relative;
  z-index: 200;
  padding-top: 12.66%;
  text-align: center;

  @include xl {
    padding-top: 15%;
  }

  @include lg {
    padding-top: 18%;
  }
}

.categories__cross {
  margin: 0 auto 80px;

  @include md {
    margin: 0 auto 63px;
  }

  @include sm {
    margin: 0 auto 28px;
  }
}

.categories__call {
  margin-bottom: 36px;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_light;

  span {
    display: inline-block;
    width: 64px;
    height: 48px;
    font-size: 52px;
    line-height: 88%;
    border-radius: 50%;
    border: 1.7px solid $color_light;
    vertical-align: top;

    @include md {
      width: 35px;
      height: 26px;
      font-size: 32px;
      line-height: 77%;
    }

    @include md {
      width: 30px;
      height: 22px;
      font-size: 26px;
      line-height: 77%;
    }
  }

  @include lg {
    font-size: 40px;
  }

  @include md {
    font-size: 30px;
  }

  @include sm {
    font-size: 24px;
    margin-bottom: 28px;
  }
}

.categories__apply {
  width: 205px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 18px 24px;
  border-radius: 50%;
  border: 1.7px solid $color_light;
  appearance: none;
  outline: none;
  background-color: transparent;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_light;
  cursor: pointer;

  @include md {
    width: 192px;
    height: 60px;
    font-size: 18px;
  }

  @include sm {
    width: 176px;
    height: 55px;
    font-size: 16px;
  }
}

.categories__reqtangle {
  position: relative;
  z-index: 100;
  width: 100%;
  min-height: 63.2%;
  padding-top: 11.3%;
  color: #f9f8ef;

  @include md {
    padding-top: 0;
  }
}

.categories__container {
  min-height: 1067px;
  padding-bottom: 47px;
  color: $color_light;
  background-color: $color-dark;

  @include md {
    padding-top: 80px;
    min-height: 776px;
  }

  @include sm {
    min-height: 710px;
  }

  @include xs {
    min-height: 607px;
    margin-top: -80px;
    padding-top: 140px;
  }
}

.categories__title {
  margin-bottom: 17px;

  @include md {
    margin-bottom: 12px;
  }

  @include sm {
    margin-bottom: 3px;
  }
}

.categories__control {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 76px;

  @include md {
    margin-bottom: 44px;
  }
}

.categories__tablink {
  position: relative;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  font-family: $mon;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-transform: uppercase;
  color: #757575;
  transition: all 0.2s linear;

  &:first-child {
    margin-right: 52px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 4px;
    left: 0;
    width: 100%;
    background-color: $color_light;
    height: 1px;
    opacity: 0;
    transition: all 0.2s linear;
  }

  &.active {
    color: $color_light;

    &::after {
      opacity: 1;
    }
  }

  @include md {
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;

    &:first-child {
      margin-right: 28px;
    }
  }
}

.categories__amount {
  font-family: "arina";
  font-size: 28px;
  line-height: 128%;
  vertical-align: super;
}

.categories__tabs {
  margin-bottom: 48px;

  @include md {
    margin-bottom: 36px;
  }
}

.categories__tab {
  display: none;
  list-style-type: none;
  padding: 0;
  margin: 0;

  &.active {
    display: block;
  }
}

.categories__item {
  text-align: center;

  &:hover .categories__link {
    opacity: 1;
  }

  &:not(:last-child) {
    margin-bottom: 6px;
  }
}

.categories__link {
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  text-decoration: none;
  color: $color_light;
  opacity: 0.4;
  transition: all 0.2s linear;

  & span {
    display: inline-block;
    vertical-align: super;
  }

  & img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include md {
    font-size: 40px;

    & span {
      width: 15px;
      vertical-align: top;
    }
  }

  @include sm {
    font-size: 30px;

    & span {
      width: 10px;
    }
  }

  @include xs {
    font-size: 28px;
  }
}
