.categories-intro {
  position: relative;
  padding: 180px 0 130px;
  overflow: hidden;

  @include lg {
    padding: 180px 0 80px;
  }

  @include sm {
    padding: 75px 0 60px;
  }
}

.categories-intro__disk {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -63%);
  width: 100%;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: block;
    width: 100%;
    height: auto;
  }

  &::after {
    content: "";
    display: block;
    padding-top: 100%;
  }
}

.categories-intro__num {
  position: relative;
  z-index: 5;
}

.categories-intro__title {
  position: relative;
  z-index: 5;
  margin-bottom: 14px;
  color: $color_light;

  @include sm {
    margin-bottom: 8px;
  }
}

.categories-intro__control {
  position: relative;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 76px;

  @include md {
    margin-bottom: 44px;
  }
}

.categories-intro__tablink {
  position: relative;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  font-family: $mon;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-transform: uppercase;
  color: #757575;
  transition: all 0.2s linear;

  &:first-child {
    margin-right: 52px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 4px;
    left: 0;
    width: 100%;
    background-color: $color_light;
    height: 1px;
    opacity: 0;
    transition: all 0.2s linear;
  }

  &.active {
    color: $color_light;

    &::after {
      opacity: 1;
    }
  }

  @include md {
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;

    &:first-child {
      margin-right: 28px;
    }
  }
}

.categories-intro__amount {
  font-family: "arina";
  font-size: 28px;
  line-height: 128%;
  vertical-align: super;
}

.categories-intro__tabs {
  position: relative;
  z-index: 5;
}

.categories-intro__tab {
  display: none;
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1240px;

  @include lg {
    max-width: 900px;
  }

  @include md {
    max-width: 780px;
  }

  &.active {
    display: flex;

    @include sm {
      display: block;
    }
  }
}

.categories-intro__item {
  display: flex;

  @include sm {
    display: block;
  }

  &:hover .categories-intro__link {
    opacity: 1;
  }

  &.non-active {
    opacity: 0.4;
  }
}

#media-1 {
  order: 2;
}

#media-2 {
  order: 1;
  & .categories-intro__star {
    display: none;
  }
}

#media-3 {
  order: 3;
}

#media-4 {
  order: 4;
  & .categories-intro__star {
    display: none;
  }
}

#media-5 {
  order: 5;
}

@include md {
  #media-1 {
    order: 1;
    & .categories-intro__star {
      display: none;
    }
  }

  #media-2 {
    order: 2;
    & .categories-intro__star {
      display: block;
    }
  }

  #media-3 {
    order: 4;
  }

  #media-4 {
    order: 3;
    & .categories-intro__star {
      display: none;
    }
  }

  #media-5 {
    & .categories-intro__star {
      display: none;
    }
  }
}

.categories-intro__star {
  margin: 0 26px;

  &.non-active {
    opacity: 0.4;
  }
}

@include sm {
  #media-4,
  #media-5 {
    & .categories-intro__star {
      display: block;
    }
  }

  .categories-intro__item {
    text-align: center;
  }

  .categories-intro__star {
    margin: 0 auto 18px;
  }
}

.categories-intro__link {
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  text-decoration: none;
  color: $color_light;
  opacity: 1;
  transition: all 0.2s linear;

  & span {
    display: inline-block;
    vertical-align: super;
  }

  & img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include lg {
    font-size: 40px;

    & span {
      width: 15px;
      vertical-align: top;
    }
  }

  @include sm {
    font-size: 30px;
    margin-bottom: 18px;

    & span {
      width: 10px;
    }
  }

  @include xs {
    font-size: 28px;
  }
}

#brand-1 {
  order: 1;
}

#brand-2 {
  order: 2;
}

#brand-3 {
  order: 3;
}

#brand-1,
#brand-3 {
  .categories-intro__star {
    display: none;
  }
}

#brand-1,
#brand-2 {
  margin-bottom: 16px;
}

@include md {
  #brand {
    max-width: 700px;
  }

  #brand-3 {
    .categories-intro__star {
      display: none;
    }
  }
}

@include sm {
  #brand-3 {
    .categories-intro__star {
      display: block;
    }
  }
}
