.contacts {
  padding: 120px 0 134px;
  background-color: $color_dark;
  box-shadow: 0px -37px 184px #f9f8ef;

  @include md {
    padding: 60px 0 90px;
  }

  @include sm {
    padding: 40px 0 68px;
  }
}

.contacts.contacts-page {
  position: relative;
  background-color: transparent;
  overflow: hidden;
  padding: 180px 0 134px;
  box-shadow: none;

  @include lg {
    padding: 180px 0 90px;
  }

  @include sm {
    padding: 100px 0 68px;
  }
}

.contacts__title {
  max-width: 750px;
  margin: 0 auto 68px;
  color: $color_light;

  @include md {
    margin-bottom: 48px;
  }

  @include sm {
    margin-bottom: 28px;
  }
}

.contacts__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 72px 0;

  @include md {
    gap: 40px 0;
  }

  @include sm {
    grid-template-columns: repeat(1, 1fr);
    gap: 23px 0;
  }
}

.contacts__item {
  text-align: center;
}

.contacts__cross {
  margin: 0 auto 24px;
}

.contacts__name {
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_light;

  @include lg {
    font-size: 45px;
  }

  @include md {
    font-size: 30px;
  }
}

.contacts__position {
  margin-bottom: 16px;
  font-family: $mon;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_light;
  opacity: 0.8;

  @include md {
    font-size: 16px;
  }
}

.contacts__email,
.contacts__phone {
  display: block;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  color: $color_light;
  text-decoration: none;

  @include md {
    font-size: 14px;
  }
}

.contacts__email {
  margin-bottom: 4px;
}
