.jury {
  position: relative;
  background-color: $color_dark;
  padding-top: 172px;

  @include md {
    padding-top: 89px;
  }

  @include sm {
    padding-top: 40px;
  }
}

.jury__bg {
  position: absolute;
  top: -10px;
  left: 0;
  width: 100%;
  height: 20px;
  z-index: 1;
  background-color: $color_dark;
}

.jury__container {
  position: relative;
  z-index: 2;
  color: $color_light;
}

.jury__title {
  max-width: fit-content;
  margin: 0 auto;
  margin-bottom: 16px;
  color: $color_light;
}

.jury__text {
  max-width: 919px;
  margin: 0 auto 44px;
  font-family: $mon;
  font-weight: 400;
  font-size: 24px;
  line-height: 132%;
  text-align: center;
  color: $color_light;

  @include lg {
    font-size: 20px;
  }

  @include md {
    margin-bottom: 28px;
    padding: 0 32px;
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;
  }

  @include xs {
    padding: 0 14px;
  }
}

.jury__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  padding: 0 40px;

  @include md {
    margin-bottom: 53px;
  }

  @include sm {
    margin-bottom: 40px;
  }
}

.jury__prev,
.jury__next {
  & span {
    display: inline-block;
    cursor: pointer;
  }

  @include xs {
    display: none;
  }
}

.jury__slider {
  width: 100%;
  overflow: hidden;
}

.jury__item {
  position: relative;

  &:after {
    content: '';
    display: block;
    padding-top: 80.88%;

    @include xl {
      padding-top: 88.88%;
    }

    @media (max-width: 370px) {
      padding-top: 95%;
    }
  }
}

.jury__item-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  z-index: 2;
}

.jury__image-wrapper {
  position: relative;
  height: 76.97%;
  margin-bottom: 40px;
  overflow: hidden;

  @include xl {
    margin-bottom: 30px;
  }

  @include lg {
    margin-bottom: 20px;
  }

  @include md {
    margin-bottom: 10px;
  }
}

.jury__disk {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 157.25%;
  height: 252.61%;
  border-radius: 50%;
  overflow: hidden;
}

.jury__img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 63.59%;
  height: 39.59%;

  & img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include xl {
    height: 37.2%;
  }
}

.jury__name,
.jury__function {
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 78%;
  text-align: center;
  text-transform: uppercase;
  color: $color_light;

  @include lg {
    font-size: 45px;
  }

  @include md {
    font-size: 30px;
  }

  @include sm {
    font-size: 24px;
  }

  @media (max-width: 370px) {
    font-size: 20px;
  }
}
