.info-black {
  margin-top: -10px;
  padding: 90px 0 164px;
  background-color: $color_dark;

  @include lg {
    padding: 72px 0 60px;
  }

  @include sm {
    padding: 68px 0 30px;
  }
}

.info-black__cross {
  margin: 0 auto 60px;

  @include lg {
    margin-bottom: 36px;
  }

  @include sm {
    margin-bottom: 28px;
  }
}

.info-black__descr {
  width: 52.76%;
  margin: 0 auto;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color-light;
  text-align: center;

  @include lg {
    width: 81.9%;
    font-size: 30px;
  }

  @include md {
    font-size: 24px;
  }

  @include sm {
    width: 85.6%;
  }
}
