	background-position: 60.966257668711656% 83.65853658536585%;
	background-size: math.div(1346, 42)*100% auto;
	width: 42px;

}
@mixin svg-pos-image-youtube {
	background-position: 60.966257668711656% 83.65853658536585%;
}
@mixin svg-image-youtube-hover {
	.svg-image-youtube {
		@include svg-pos-image-youtube-hover
	}
}

.svg-image-youtube:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(42, 42)*100%;
}

.svg-image-youtube-dims {
	width: 42px;
	height: 42px;
}


.image-by-height { width:auto; }
.image-by-height:before { content:none; }
.image-by-height > svg { height: inherit; }
.image-by-height > img { height: inherit !important; vertical-align: top !important; margin: 0 !important; padding: 0 !important; max-width: none !important; max-height: none !important; border: 0 !important; opacity: 0 !important; }