.info-purple {
  padding: 78px 0 220px;

  @include lg {
    padding: 80px 0 110px;
  }

  @include sm {
    padding: 40px 0;
  }
}

.info-purple__cross {
  margin: 0 auto 60px;

  @include lg {
    margin-bottom: 36px;
  }

  @include sm {
    margin-bottom: 28px;
  }
}

.info-purple__descr {
  width: 52.76%;
  margin: 0 auto;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_dark;
  text-align: center;

  @include lg {
    width: 55%;
    font-size: 30px;
  }

  @include md {
    font-size: 24px;
  }

  @include sm {
    width: 63%;
  }
}
