.award {
  position: relative;
  padding: 180px 0 130px;
  overflow: hidden;

  @include lg {
    padding: 180px 0 80px;
  }

  @include sm {
    padding: 90px 0 60px;
  }
}

.award__disk {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -63%);
  width: 100%;

  @include xl {
    transform: translate(-50%, -58%);
  }

  @include lg {
    transform: translate(-50%, -48%);
  }

  @include sm {
    transform: translate(-50%, -55%);
  }

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: block;
    width: 100%;
    height: auto;
  }

  &::after {
    content: '';
    display: block;
    padding-top: 100%;
  }
}

.award__num {
  position: relative;
  z-index: 5;
}

.award__title {
  position: relative;
  z-index: 5;
  margin: 0 auto 60px;
  color: $color_dark;

  @include md {
    margin-bottom: 28px;
  }

  @include sm {
    max-width: 220px;
  }
}

.award__icon {
  width: 251px;
  height: 274px;
  margin: 0 auto 56px;
  border-radius: 500px 500px 0px 0px;

  @include md {
    width: 138px;
    height: 150px;
    margin: 0 auto 40px;
  }

  @include sm {
    width: 110px;
    height: 120px;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.award__descr {
  max-width: 970px;
  margin: 0 auto;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-align: center;
  text-transform: uppercase;
  color: $color_black;

  @include md {
    font-size: 30px;
  }

  @include sm {
    margin-bottom: 15px;
  }

  .award__descr-2 {
    margin-bottom: 8px;
  }
}

.award__text {
  max-width: 754px;
  margin: 0 auto 102px;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  text-align: center;
  color: $color_black;

  @include md {
    margin-bottom: 80px;
    font-size: 14px;
  }

  @include sm {
    margin-bottom: 39px;
  }
}

.award__block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 115px;
  border-top: 1.7px solid $color_black;
  border-bottom: 1.7px solid $color_black;

  @include md {
    margin-bottom: 59px;
  }

  @include sm {
    grid-template-columns: repeat(1, 1fr);
    margin-bottom: 30px;
  }
}

.award__info {
  padding: 55px 0 58px;
  font-family: $mon;
  font-weight: 400;
  font-size: 24px;
  line-height: 136%;
  color: $color_black;

  &:first-child {
    border-right: 1.7px solid $color_black;
    padding-right: 74px;
  }

  &:last-child {
    padding-left: 52px;
  }

  @include md {
    font-size: 18px;
    padding: 40px 0 44px;

    &:first-child {
      padding-right: 24px;
    }

    &:last-child {
      padding-left: 20px;
    }
  }

  @include sm {
    margin: 0 auto;
    text-align: center;
    font-size: 16px;

    &:first-child,
    &:last-child {
      padding: 20px 30px 24px;
      border-right: none;
    }

    &:first-child {
      border-bottom: 1.7px solid $color_black;
    }
  }

  @include xs {
    &:first-child,
    &:last-child {
      padding: 20px 19px 24px;
    }
  }
}

.award__cross {
  margin: 0 auto 60px;

  @include lg {
    margin-bottom: 36px;
  }

  @include sm {
    margin-bottom: 28px;
  }
}

// ======== schedule styles ========

.schedule {
  padding-bottom: 128px;
}

.schedule__wrapper {
  @include sm {
    margin: 0 -14px;
  }
}

.schedule__top,
.schedule__bottom {
  position: relative;
  z-index: -1;
  width: 100%;
  padding-top: 19.35%;
  overflow: hidden;

  .schedule__figure {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('./../images/award/award-top.svg') top center / cover
      no-repeat;
  }
}

.schedule__bottom {
  .schedule__figure {
    bottom: 0;
    background: url('./../images/award/award-bottom.svg') bottom center / cover
      no-repeat;
  }
}

.schedule__block {
  margin: -5px 0;
}

.schedule__block-wrapper {
  padding: 5px 0;
  background-color: $color_dark;

  @include lg {
    padding: 20px 0 5px;
  }

  @include md {
    padding: 5px 0;
  }

  @include xs {
    padding: 20px 0 5px;
  }
}

.schedule__topic {
  max-width: 740px;
  margin: 0 auto;
  font-family: $arina;
  font-weight: 400;
  font-size: 118px;
  line-height: 78%;
  text-align: center;
  text-transform: uppercase;
  color: $color_light;

  @include md {
    max-width: 500px;
    font-size: 70px;
  }

  @include sm {
    max-width: 320px;
    font-size: 45px;
  }

  &.schedule__topic-1 {
    margin-bottom: 12px;
  }

  &.schedule__topic-3 {
    margin-top: 200px;

    @include md {
      margin-top: 140px;
    }

    @include sm {
      margin-top: 100px;
    }
  }
}

.schedule__period {
  margin: 0 auto 200px;
  font-family: $mon;
  font-weight: 400;
  font-size: 18px;
  line-height: 132%;
  text-align: center;
  color: $color_light;

  p {
    display: inline-block;
  }

  @include md {
    margin: 0 auto 140px;
    font-size: 18px;
  }

  @include sm {
    margin: 0 auto 100px;
    font-size: 16px;

    p {
      display: block;
    }
  }
}

.schedule__cross {
  margin: 44px auto;

  @include md {
    margin: 40px auto;
  }

  @include sm {
    margin: 24px auto;
  }
}

.schedule__name {
  margin: 0 auto 16px;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 78%;
  text-align: center;
  text-transform: uppercase;
  color: $color_light;

  @include md {
    font-size: 40px;
  }

  @include sm {
    margin: 0 auto 4px;
    font-size: 30px;
  }
}

.schedule__date,
.schedule__price {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  text-align: center;
  color: $color_light;

  @include sm {
    font-size: 16px;
  }
}

.schedule__date {
  margin-bottom: 8px;

  @include md {
    margin-bottom: 6px;
  }

  @include sm {
    margin-bottom: 0;
  }
}
