.intro {
  position: relative;
  min-height: 100vh;
  padding-top: 100px;
  padding-bottom: 46px;
  display: flex;
  overflow: hidden;

  & .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @include md {
    min-height: auto;
    padding-top: 75px;
    padding-bottom: 41px;
  }

  @include xs {
    min-height: auto;
    padding-top: 55px;
  }
}

.intro__disk {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -53%);
  // width: 1044px;
  width: 100%;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: block;
    width: 100%;
    height: auto;
  }

  &::after {
    content: "";
    display: block;
    padding-top: 100%;
  }
}

.intro__logo {
  position: relative;
  z-index: 5;
  margin: 90px 41px 0;
  padding-bottom: 100px;

  img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include xl {
    margin: 70px 41px 0;
    padding-bottom: 70px;
  }

  @include md {
    margin: 35px 0 0;
    padding-bottom: 48px;
  }
}

.intro__main {
  position: relative;
  z-index: 5;
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 41px 60px;

  @include xl {
    margin: 0 41px 40px;
  }

  @include lg {
    margin: 0 0 40px;
  }

  @include md {
    margin: 0 0 28px;
  }

  @include xs {
    position: relative;
    justify-content: center;
    margin: 0 0 20px;
  }
}

.intro__cross {
  transform: translateY(-28px);

  @include xs {
    position: absolute;
    top: 50%;

    &:first-child {
      left: 0;
    }

    &:last-child {
      right: 0;
    }
  }
}

.intro__title {
  font-family: $arina;
  color: $color_dark;

  @include xs {
    margin: 0 15px;
  }

  @media (max-width: 480px) {
    font-size: 35px;
  }

  @media (max-width: 370px) {
    font-size: 28px;
  }
}

.intro__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro__item {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: $color_dark;

  @include lg {
    font-size: 18px;
  }

  @include md {
    font-size: 14px;
  }

  @include xs {
    font-size: 12px;
  }
}

.intro__add {
  display: flex;
  justify-content: center;
  align-items: center;

  & .intro__item:not(:last-child) {
    margin-right: 80px;
  }

  @include md {
    & .intro__item:not(:last-child) {
      margin-right: 40px;
    }
  }

  @include sm {
    display: block;
    text-align: center;

    & .intro__item:not(:last-child) {
      margin-right: 0;
    }
  }
}
