.oficial__img {
  width: 100%;
  height: 286px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border: 1.7px solid $color_dark;
  border-radius: 50%;

  @media (max-width: 1439px) {
    height: 248.7px;
  }

  @include lg {
    height: 145px;
    grid-template-rows: repeat(2, 145px);
    margin-bottom: 12px;
  }

  @media (max-width: 370px) {
    height: 124px;
  }
}

.oficial__block {
  width: 152px;

  @include lg {
    width: 80px;
    height: 80px;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.oficial__name {
  font-family: $mon;
  font-size: 16px;
  line-height: 132%;
  text-align: center;
  color: $color_dark;

  @include lg {
    font-size: 14px;
    line-height: 136%;
  }

  @media (max-width: 370px) {
    font-size: 11px;
  }
}
