.category {
  position: relative;
  padding: 210px 0 130px;
  overflow: hidden;
  // background-color: $color-dark;

  @include lg {
    padding: 195px 0 80px;
  }

  @include md {
    padding: 110px 0 60px;
  }
}

.category__disk {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -53%);
  // width: 1044px;
  width: 100%;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: block;
    width: 100%;
    height: auto;
  }

  &::after {
    content: '';
    display: block;
    padding-top: 100%;
  }
}

.category__title-wrapper {
  display: grid;
  justify-content: center;
  align-items: flex-start;
  grid-template-columns: auto 38px;
  gap: 0 8px;
  margin-bottom: 16px;

  @include lg {
    grid-template-columns: min-content 32px;
    gap: 0 4px;
    margin-bottom: 12px;
  }

  @include sm {
    grid-template-columns: min-content 17px;
    margin-bottom: 20px;
  }
}

.category__title {
  position: relative;
  z-index: 5;
  // margin-bottom: 16px;
  color: $color-dark;

  // @include sm {
  //   margin-bottom: 8px;
  // }
}

.category__title-icon {
  img {
    position: relative;
    top: -23px;
    display: block;
    width: 100%;
    height: auto;

    @include xl {
      top: -15px;
    }

    @include xl {
      top: -10px;
    }
  }
}

.category__control {
  position: relative;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 72px;

  @include md {
    margin-bottom: 44px;
  }

  @include sm {
    display: block;
  }
}

.category__nontabs {
  & .category__control {
    visibility: hidden;
  }
}

.category__tablink {
  position: relative;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  font-family: $mon;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-transform: uppercase;
  color: #757575;
  transition: all 0.2s linear;

  &:not(:last-child) {
    margin-right: 52px;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    background-color: $color_dark;
    height: 1px;
    opacity: 0;
    transition: all 0.2s linear;
  }

  &.active {
    color: $color_dark;

    &::after {
      opacity: 1;
    }
  }

  @include md {
    font-size: 18px;
  }

  @include sm {
    display: block;
    margin: 0 auto;
    font-size: 16px;

    &:first-child {
      margin-right: auto;
      margin-bottom: 15px;
    }
  }
}

.category__accordions {
  position: relative;
  z-index: 5;
  display: none;
  grid-template-columns: repeat(2, 1fr);
  // grid-template-areas:
  //   'cat-1 cat-2'
  //   'cat-3 cat-4'
  //   'cat-5 cat-6';
  border-top: 1.7px solid $color_black;

  &.active {
    display: grid;
  }

  @include sm {
    grid-template-columns: 1fr;
  }
}

.category__accordion-item {
  border-bottom: 1.7px solid $color_black;

  @include sm {
    border-bottom: none;
  }

  // &:nth-child(1) {
  //   grid-area: cat-1;
  // }

  // &:nth-child(2) {
  //   grid-area: cat-2;
  // }

  // &:nth-child(3) {
  //   grid-area: cat-3;
  // }

  // &:nth-child(4) {
  //   grid-area: cat-4;
  // }

  // &:nth-child(5) {
  //   grid-area: cat-5;
  // }

  // &:nth-child(6) {
  //   grid-area: cat-6;
  // }
}

.category__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 140px 60px 0;
  border-right: 1.7px solid $color_black;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  // line-height: 78%;
  line-height: 1.2;
  text-transform: uppercase;
  color: $color_dark;
  overflow: hidden;

  @include lg {
    font-size: 40px;
  }

  @include md {
    padding: 30px 40px 11px 0;
    font-size: 40px;
  }

  @include sm {
    display: grid;
    grid-template-columns: 1fr 21px;
    gap: 0 8px;
    cursor: pointer;
    border-right: none;
    padding: 24px 0 16px 0;
    font-size: 30px;
    line-height: 128%;
  }
}

.svg-image-category-plus {
  display: none;

  @include sm {
    display: inline-block;
    width: 21px;
    height: 21px;
    transition: all 0.2s linear;
  }
}

.svg-image-category-minus {
  display: none;
}

@include sm {
  .open .svg-image-category-plus {
    display: none;
  }

  .open .svg-image-category-minus {
    display: block;
  }
}

.category__info {
  padding: 44px 52px 44px 48px;

  @include md {
    padding: 35px 43px 39px 20px;
  }

  @include sm {
    padding: 0;
    border-bottom: 1.7px solid $color_black;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }
}

.category__text {
  margin-bottom: 24px;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  color: $color_black;

  @include md {
    font-size: 18px;
  }

  @include sm {
    border-top: 1.7px solid $color_black;
    padding-top: 15px;
    font-size: 16px;
  }
}

.category__criteria {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  cursor: pointer;

  @include sm {
    cursor: default;
    font-size: 20px;
  }

  p {
    margin-right: 8px;
    font-family: $mon;
    font-weight: 400;
    font-size: 24px;
    line-height: 136%;
    text-transform: uppercase;
    color: $color_dark;
  }

  span {
    display: inline-block;
    width: 18px;
    height: 8px;
    transition: all 0.2s linear;

    @include sm {
      visibility: hidden;
    }
  }
}

.category__description {
  // display: none;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;

  @include sm {
    max-height: none;
  }
}

.category__list {
  margin-bottom: 40px;
}

.category__item {
  display: grid;
  grid-template-columns: 36px 1fr;
  align-items: center;
  margin-bottom: 16px;

  @include md {
    margin-bottom: 14px;
  }

  @include sm {
    margin-bottom: 10px;
  }
}

.category__name {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  color: $color_black;

  @include md {
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;
  }
}

.category__apply {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 197px;
  height: 64px;
  border: 1.7px solid $color_dark;
  border-radius: 50%;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_dark;
  cursor: pointer;

  @include md {
    font-size: 18px;
  }

  @include sm {
    width: 176px;
    height: 55px;
    margin: 0 auto 35px;
    font-size: 16px;
  }
}

// show / hide tabs

.category__criteria.open .svg-image-category-arrow {
  transform: rotate(180deg);
}
