.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100px;
  background-color: transparent;
  transition: all 0.2s ease-in-out;

  .container {
    height: 100%;
  }

  &.sticky {
    background-color: $color_purple;
  }

  &.hide {
    transform: translateY(-100%);
  }

  @include md {
    height: 75px;
    padding-top: 24px;
  }

  @include xs {
    height: 55px;
    padding: 15px 0 0 0;
  }
}

.header__wrapper {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid $color_dark;
  overflow: hidden;

  @include md {
    align-items: flex-start;
  }

  @include xs {
    align-items: center;
    padding-bottom: 15px;
  }
}

.header__disk {
  position: absolute;
  top: -70px;
  left: 50%;
  z-index: -1;
  width: 444px;
  height: 452px;
  background: url("./../images/intro/intro-disk.svg") top center / contain
    no-repeat;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 0.75;
    visibility: visible;
  }

  @include md {
    display: none;
  }
}

.header__menu,
.header__login,
.header__apply {
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_dark;
  cursor: pointer;

  @include lg {
    font-size: 18px;
  }

  @include md {
    font-size: 16px;
  }

  @include sm {
    font-size: 14px;
  }
}

.header__menu {
  transition: color 0.2s ease-in-out;
  &.active {
    color: $color_light;
  }

  @include md {
    display: none;
  }
}

.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 207px;

  a {
    display: block;
    width: 100%;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include md {
    position: static;
    transform: translate(0, 0);
    width: 115px;
  }

  @include xs {
    width: 73px;
  }
}

.header__control {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include md {
    display: none;
  }
}

.header__login {
  margin-right: 40px;

  a {
    color: inherit;
    text-decoration: none;
  }
}

.header__apply {
  padding: 18px 24px;
  border-radius: 50%;
  border: 1.7px solid $color_dark;
  appearance: none;
  outline: none;
  background-color: transparent;
}

.header__control-small {
  display: none;

  @include md {
    display: flex;
    align-items: center;
  }
}

.header__login-small {
  width: 33px;
  height: 33px;
  margin-right: 22px;
  cursor: pointer;

  a {
    display: block;
  }

  span {
    display: block;
    width: 100%;
    height: auto;
  }

  @include xs {
    width: 25px;
    height: 25px;
  }
}

.header__menu-small {
  width: 28px;
  height: 24px;
  cursor: pointer;

  span {
    display: block;
    width: 100%;
    height: auto;
  }
  @include xs {
    width: 23px;
    height: 16px;
  }
}

// ================ HEADER NAVBAR ================

.header__navbar {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;

  &.active {
    max-height: 64px;
  }

  &.sticky {
    background-color: $color_purple;
  }
}

.header__list {
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 18px 0;
  border-bottom: 2px solid $color_dark;
}

.header__item:not(:last-child) {
  margin-right: 40px;
}

.header__link {
  text-decoration: none;
  color: inherit;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_dark;
  transition: all 0.2s linear;

  &:hover {
    color: $color_light;
  }
}
