// overlay and modals style

.overlay {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000000000;
    background: rgba(30, 30, 30, 0.7);
    cursor: pointer;

    &.active {
        display: block;
    }
}

.experts__modal {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 1000000001;
    width: 580px;
    height: 279px;
    transform: translate(-50%, -50%);
    background: $color_purple;

    &.active {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    @include xs {
        width: 90%;
    }
}

.experts__close {
    position: absolute;
    top: 32px;
    right: 32px;
    cursor: pointer;
}

.experts__msg {
    font-family: $arina;
    font-weight: 400;
    font-size: 40px;
    line-height: 100%;
    text-align: center;
    text-transform: uppercase;
    color: $color_dark;

    @include xs {
        font-size: 34px;
    }
}

// redefine styles from _profile.scss
// for experts-application-list.html

.experts__list,
.experts__work-intro {
    position: relative;
    z-index: 1000;
    padding: 210px 0 50px;
    overflow: unset;

    @include lg {
        padding: 180px 0 50px;
    }

    @include md {
        padding: 110px 0 50px;
    }
}

.experts__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.profile__control.experts__control {
    display: block;
}

.myworks__form {
    max-width: 880px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 150px 1fr;
    // align-items: center;
    // align-content: center;
    gap: 0 48px;

    .experts__selects {
        height: 100%;
    }

    .choices__inner {
        display: flex;
        align-items: center;
    }

    .experts__selects {
        margin-bottom: 0;
    }

    .experts__selects .choices__list--dropdown {
        left: 0;
        transform: translateX(0);

        @include md {
            left: 50%;
            transform: translateX(-50%);
        }
    }

    @include md {
        grid-template-columns: 1fr;

        .experts__selects {
            margin-bottom: 20px;
        }
    }

    @include sm {
        max-width: 300px;
        display: block;

        .experts__selects {
            margin-bottom: 28px;
        }
    }
}

.experts__selects {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 414px;
    margin: 0 auto 10px;

    @include sm {
        display: block;
        max-width: 500px;
        margin: 0 auto;
    }

    &.longlist__selects {
        @include sm {
            display: grid;
            grid-template-columns: max-content;
            justify-content: center;
        }
    }

    .choices__inner {
        background-color: transparent;
        border: none;
    }

    .choices__list--dropdown {
        left: 50%;
        transform: translateX(-50%);
        min-width: 740px;

        @media (max-width: 1024px) {
            min-width: 550px;
        }

        @include md {
            min-width: 430px;
        }

        @include xs {
            min-width: calc(100% + 32px);
        }
    }

    .choices__list--single {
        padding-left: 30px;
        &::after {
            right: unset;
            left: 0;
            top: 3px;
        }
    }
}

.experts__categories,
.experts__awards {
    .choices__item--selectable {
        font-size: 15px;
        font-weight: 600;
        line-height: 100%;

        @include sm {
            font-size: 18px;
        }
    }

    @include sm {
        margin-bottom: 20px;
    }
}

.experts__filters {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 500px;
    margin: 0 auto;

    @include sm {
        display: block;
    }
}

.experts__filter {
    min-width: fit-content;
    &:not(:last-child) {
        margin-right: 48px;
    }

    @include sm {
        &:not(:last-child) {
            margin: 0 auto 48px;
        }
    }
}

.experts__checkbox {
    width: 0;
    visibility: hidden;
    opacity: 0;

    &:checked + .experts__label .experts__inner {
        display: block;
    }
}

.experts__label {
    position: relative;
    margin: 0 auto;
    padding-left: 24px;
    font-family: $mon;
    font-weight: 600;
    font-size: 15px;
    line-height: 100%;
    color: $color_dark;
    cursor: pointer;

    .experts__square {
        position: absolute;
        top: 50%;
        left: 0;
        width: 16px;
        height: 16px;
        z-index: 5;
        transform: translateY(-50%);
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid $color_dark;
    }

    .experts__inner {
        display: none;
        width: 8px;
        height: 8px;
        background-color: $color_dark;
    }

    @include sm {
        font-size: 18px;
    }
}

.experts__wrapper {
    padding: 50px 0 120px;

    .profile__works-image {
        position: relative;
    }

    .profile__mark {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 3;
        padding: 10px 18px;
        font-family: $mon;
        font-weight: 400;
        font-size: 15px;
        line-height: 100%;
        color: $color_purple;
        background-color: $color_dark;
    }
}

// experts-application-long-list.html

.category__header.profile__header.experts__title {
    @include sm {
        margin-bottom: 28px;
        font-size: 34px;
        line-height: 100%;
    }
}

.project {
    display: grid;
    grid-template-columns: 235px 1fr;
    gap: 0 21px;
    padding-bottom: 30px;
    border-bottom: 1px solid rgba(30, 30, 30, 0.3);

    &:not(:last-child) {
        margin-bottom: 24px;
    }

    @include sm {
        grid-template-columns: 1fr;
        gap: 30px 0;

        &:not(:last-child) {
            margin-bottom: 28px;
        }
    }
}

.project__img {
    position: relative;
}

.project__mark {
    position: absolute;
    top: 7px;
    left: 0;
    z-index: 3;
    padding: 5px 14px;
    font-family: $mon;
    font-weight: 400;
    font-size: 15px;
    color: white;
    background-color: transparent;
}

.project__img {
    img {
        display: block;
        width: 100%;
        height: auto;
    }
}

.project__content {
    overflow: hidden;
}

.project__title,
.project__category,
.project__description,
.project__option {
    font-family: $mon;
    font-weight: 400;
    color: $color_dark;

    @include sm {
        text-align: center;
    }
}

.project__title {
    font-size: 23px;
    line-height: 128%;
    text-transform: uppercase;

    a {
        text-decoration: none;
        color: inherit;
    }
}

.project__category {
    margin-bottom: 13px;
    font-size: 20px;
    line-height: 150%;
    opacity: 0.5;
}

.project__description {
    margin-bottom: 22px;
    font-size: 15px;
    line-height: 100%;
    opacity: 0.5;

    @include sm {
        margin-bottom: 30px;
    }
}

.project__choice {
    max-width: 900px;
    overflow: hidden;
    margin-left: 0;
    margin-right: 0;
}

.project__option {
    display: inline-block;
    // max-width: 180px;
    width: max-content;
    height: 60px;
    padding: 17px 20px;
    border: 1px dashed $color_dark;
    border-radius: 20px;
    background-color: transparent;
    appearance: none;
    outline: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 20px;
    line-height: 128%;
    text-transform: uppercase;
    opacity: 0.5;

    @include lg {
        max-width: none;
    }

    &:not(:last-child) {
        margin-right: 20px;
    }

    &.active {
        color: white;
        background-color: $color_dark;
        opacity: 1;
    }
}

// experts work rating

.experts__nav {
    position: relative;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.experts__link {
    position: relative;
    font-family: $mon;
    font-weight: 600;
    font-size: 15px;
    line-height: 100%;
    text-decoration: none;
    color: rgba(30, 30, 30, 0.5);

    @include xs {
        max-width: min-content;
    }
}

.experts__prev,
.experts__next {
    &::before {
        content: "";
        position: absolute;
        top: 4px;
        width: 18px;
        height: 10px;
        transition: all 0.2s ease-out;

        @include xs {
            top: 10px;
        }
    }
}

.experts__prev {
    padding-left: 36px;

    @include xs {
        padding-left: 20px;
    }

    &::before {
        left: 0;
        background: url("./../images/experts/arrow-prev-experts.svg") center /
            cover no-repeat;
    }

    &:hover {
        &::before {
            transform: translateX(-5px);
        }
    }
}

.experts__next {
    padding-right: 36px;

    @include xs {
        padding-right: 20px;
        text-align: right;
    }

    &::before {
        right: 0;
        background: url("./../images/experts/arrow-next-experts.svg") center /
            cover no-repeat;
    }

    &:hover {
        &::before {
            transform: translateX(5px);
        }
    }
}

.profile__header.work__title {
    margin-bottom: 32px;
}

.work__text {
    font-family: $mon;
    font-size: 20px;
    font-weight: 400;
    line-height: 150%;
    color: $color_black;
}

.description {
    display: grid;
    grid-template-columns: 434px 1fr;
    gap: 0 35px;
    margin-bottom: 65px;

    @include lg {
        grid-template-columns: 340px 1fr;
        gap: 0 30px;
    }

    @include md {
        grid-template-columns: 1fr;
        gap: 20px 0;
    }
}

.description__image {
    width: 100%;
    height: 271px;
    margin-top: 40px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @include lg {
        height: 240px;
        margin-top: 30px;
    }

    @include md {
        width: 434px;
        height: 271px;
        margin-top: 0;
        justify-self: center;
    }

    @include xs {
        width: 100%;
    }
}

.description__item {
    display: grid;
    grid-template-columns: 228px 1fr;
    gap: 0 42px;
    padding: 10px 0;

    &:not(:last-child) {
        border-bottom: 1px solid rgba(30, 30, 30, 0.3);
    }

    @include lg {
        gap: 0 30px;
    }

    @include xs {
        grid-template-columns: 1fr;
        gap: 0;
    }
}

.description__content {
    line-height: 200%;

    @include xs {
        line-height: 250%;
    }
}

.description__name {
    font-weight: 700;
    line-height: 200%;
}

.general {
    margin-bottom: 52px;
}

.work__header {
    margin-bottom: 25px;
    font-family: $arina;
    font-weight: 400;
    font-size: 30px;
    line-height: 100%;
    text-transform: uppercase;
    color: $color_black;
}

.general__item {
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(30, 30, 30, 0.3);

    &:not(:last-child) {
        margin-bottom: 25px;
    }

    @include sm {
        padding-bottom: 45px;
    }
}

.general__about {
    margin-bottom: 9px;
    opacity: 0.5;
}

.results {
    margin-bottom: 83px;

    @include sm {
        margin-bottom: 70px;
    }
}

.results__sources {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 31px;
    padding-bottom: 14px;
    border-bottom: 1px solid rgba(30, 30, 30, 0.3);

    @include md {
        grid-template-columns: 1fr;
        gap: 13px 0;
        padding-bottom: 21px;
    }
}

.results__item {
    display: flex;

    @include md {
        &:not(:last-child) {
            padding-bottom: 14px;
            border-bottom: 1px solid rgba(30, 30, 30, 0.3);
        }
    }

    @include xs {
        display: block;
    }
}

.results__name {
    margin-right: 21px;
    font-weight: 700;
    line-height: 200%;
}

.results__link {
    color: $color_black;
    line-height: 200%;
}

.files {
    margin-bottom: 53px;

    @include sm {
        margin-bottom: 44px;
    }
}

.files__link {
    line-height: 200%;
}

.nominate {
    margin-bottom: 42px;

    @include sm {
        margin-bottom: 65px;
    }
}

.nominate__title {
    margin-bottom: 22px;
    font-weight: 700;
    line-height: 200%;

    @include sm {
        margin-bottom: 12px;
    }
}

.nominate__categories {
    display: flex;
    margin-bottom: 31px;

    @include sm {
        display: block;
        margin-bottom: 22px;
    }
}

.nominate__category {
    &:not(:last-child) {
        margin-right: 91px;
    }

    @include sm {
        &:not(:last-child) {
            margin-right: 0;
            margin-bottom: 8px;
        }
    }
}

.nominate__label {
    padding-left: 55px;
    font-family: $mon;
    font-size: 20px;
    font-weight: 400;
    line-height: 200%;
    color: $color_black;
}

.nominate__square.experts__square {
    width: 35px;
    height: 35px;
    border: 1px solid $color_black;
}

.nominate__inner.experts__inner {
    width: 15px;
    height: 15px;
}

.work__nav {
    display: flex;
    justify-content: flex-end;

    & .experts__link {
        min-width: max-content;

        &::before {
            top: 4px;
        }
    }
}

// winners style

.winners {
    border-bottom: 2px solid $color_dark;
}

.winners__title {
    position: relative;
    width: min-content;
    margin: 0 auto 28px;
}

.winners__icon {
    position: absolute;
    top: 0;
    right: -80px;

    @include sm {
        width: 30px;
        height: 15px;
        right: -45px;
    }

    @media (max-width: 355px) {
        right: -35px;
    }
}

.winners__nav {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    overflow: hidden;
}

.winners__years {
    display: flex;
    overflow: auto;
}

.winners__year {
    display: inline-block;
    font-family: $mon;
    font-weight: 400;
    font-size: 24px;
    line-height: 136%;
    text-transform: uppercase;
    text-decoration: none;
    color: $color_dark;

    &:not(:last-child) {
        margin-right: 61px;
    }

    &.active {
        font-weight: 700;
    }
}

.winners__container {
    position: relative;
    padding: 42px 44px 174px;

    @include sm {
        padding: 42px 0;
    }
}

.winners__item {
    margin-bottom: 10px;
    background-color: $color_dark;

    &.active {
        svg.winners__svg {
            transform: scaleY(-1);
        }

        path.winners__path {
            stroke: $color_purple;
        }

        .winners__header {
            color: $color_purple;
        }
    }
}

.winners__top {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 17px;
    cursor: pointer;

    & svg {
        flex-shrink: 0;
    }

    & svg,
    & path {
        transition: all 0.2s ease-out;
    }

    @include xs {
        justify-content: space-between;
        padding: 30px 12px;
    }
}

.winners__header {
    position: relative;
    padding: 0 28px;
    font-family: $arina;
    font-weight: 400;
    font-size: 40px;
    line-height: 78%;
    text-transform: uppercase;
    color: $color_white;
    transition: all 0.2s ease-out;
    text-align: center;

    @include sm {
        font-size: 34px;
    }

    @media (max-width: 360px) {
        padding: 0 12px;
        width: min-content;
    }
}

// winners content style

.winners__content {
    display: none;
    padding: 16px 0 65px;
}

.winners__item.active {
    .winners__content {
        display: block;
    }
}

.winners__content-item {
    &:not(:last-child) {
        margin-bottom: 37px;
    }
}

.winners__star {
    margin: 0 auto 30px;

    @include sm {
        margin-bottom: 14px;
    }
}

.winners__category {
    position: relative;
    margin-bottom: 21px;

    &::after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        height: 1px;
        z-index: 1;

        background-color: rgba(255, 255, 255, 0.5);
    }
}

.winners__category-name {
    width: max-content;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    z-index: 2;
    font-family: $arina;
    font-weight: 400;
    font-size: 32px;
    line-height: 200%;
    text-align: center;
    text-transform: uppercase;
    color: $color_white;
    background-color: $color_dark;

    @media (max-width: 360px) {
        padding: 0 10px;
    }
}

.winners__list.profile__works {
    padding: 0 8px;

    .profile__works-title,
    .profile__works-category {
        color: $color_white;
    }

    .profile__works-item {
        cursor: pointer;
    }

    .profile__works-item .profile__works-image {
        position: relative;

        &::before {
            position: absolute;
            top: 7px;
            left: 0;
            z-index: 3;
            padding: 3px 16px 7px;

            font-family: $mon;
            font-weight: 400;
            font-size: 15px;
            line-height: 100%;
            color: $color_white;
        }
    }

    .gold .profile__works-image {
        &:before {
            content: "";
            background-color: #f2cf51;
        }
    }

    .silver .profile__works-image {
        &:before {
            content: "";
            background-color: #a19c90;
        }
    }

    .bronz .profile__works-image {
        &:before {
            content: "";
            background-color: #a88815;
        }
    }
}

.winners__list-short {
    .profile__works-category {
        display: none;
    }
}

// brand-media styles

.brand__header {
    color: $color_white;
}

.brand__content {
    display: none;
    padding-bottom: 58px;
    border-bottom: 1px solid $color_white;

    @include sm {
        padding-bottom: 40px;
    }

    .profile__works-item {
        margin-bottom: 58px;

        @include md {
            &:not(:last-child) {
                margin-bottom: 0;
            }
        }

        @include sm {
            &:last-child {
                margin-bottom: 40px;
            }
        }
    }
}

.brand__item.active {
    .brand__content {
        display: grid;
    }
}

.brand__item {
    margin-bottom: 10px;

    &.active {
        svg.brand__svg {
            transform: scaleY(-1);
        }

        path.brand__path {
            stroke: $color_purple;
        }

        .brand__header {
            color: $color_purple;
        }
    }
}

.brand__top {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 17px;
    cursor: pointer;

    & svg {
        flex-shrink: 0;
    }

    & svg,
    & path {
        transition: all 0.2s ease-out;
    }
}

.brand__header {
    position: relative;
    padding: 0 28px;
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 138%;
    text-transform: uppercase;
    color: $color_white;
    transition: all 0.2s ease-out;
    text-align: center;
}

// general popup styles

.popup {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 1000000001;
    width: 93%;
    height: 80vh;
    min-height: 320px;
    transform: translate(-50%, -50%);
    background: $color_purple;
    overflow-y: auto;

    &.active {
        display: block;
    }

    @include sm {
        width: 100%;
    }
}

.popup__top {
    position: relative;
    min-height: 115px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 22px 80px 22px 42px;
    border-bottom: 2px solid $color_dark;
}

.popup__title {
    font-family: $mon;
    font-weight: 700;
    font-size: 40px;
    line-height: 136%;
    text-transform: uppercase;
    color: $color_dark;

    @include md {
        font-size: 34px;
    }

    @include xs {
        font-size: 28px;
    }
}

.popup__text--bold,
.popup__text--regular {
    font-family: $mon;
    font-weight: 400;
    font-size: 20px;
    line-height: 200%;
    color: $color_dark;
}

.popup__text--bold {
    font-weight: 700;
}

// mediapopup styles

.mediapopup__wrapper {
    padding: 28px 28px 72px;

    @include sm {
        padding: 34px 10px 63px;
    }
}

.mediapopup__img {
    margin-bottom: 39px;

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include sm {
        margin-bottom: 19px;
    }
}

.mediapopup__item {
    display: grid;
    grid-template-columns: 346px 1fr;
    gap: 0 44px;
    padding: 10px 0;

    &:not(:last-child) {
        border-bottom: 1px solid rgba(30, 30, 30, 0.3);
    }

    @include sm {
        grid-template-columns: 1fr;

        &:not(:last-child) {
            padding-bottom: 16px;
        }
    }
}

// special popup wrapper

.specpopup__wrapper {
    padding: 68px 40px 45px;

    @include sm {
        padding: 64px 12px 41px;
    }
}

.specpopup__item {
    &:not(:last-child) {
        padding-bottom: 58px;
        margin-bottom: 60px;
        border-bottom: 1px solid rgba(30, 30, 30, 0.3);
    }
}

.specpopup__header {
    margin-bottom: 46px;
    font-family: $arina;
    font-weight: 400;
    font-size: 45px;
    line-height: 78%;
    text-transform: uppercase;
    color: $color_dark;

    @include md {
        margin: 0 auto 46px;
        text-align: center;
    }
}

.specpopup__content {
    display: grid;
    grid-template-columns: 256px 1fr;
    gap: 0 53px;

    @include md {
        grid-template-columns: 1fr;
        gap: 23px 0;
    }
}

.specpopup__img {
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include md {
        margin: 0 auto;
        max-width: 350px;
    }

    @include xs {
        max-width: 75%;
    }
}

.specpopup__name {
    margin-bottom: 24px;
    padding-bottom: 25px;
    font-size: 24px;
    line-height: 150%;
    border-bottom: 1px solid rgba(30, 30, 30, 0.3);

    @include md {
        text-align: center;
        margin: 0 auto 24px;
    }
}

.specpopup__info {
    display: grid;
    grid-template-columns: 160px 1fr;
    gap: 0 14%;

    &:not(:last-child) {
        margin-bottom: 20px;
    }

    @include lg {
        gap: 0 8%;
    }

    @include xs {
        grid-template-columns: 1fr;
    }
}

.specpopup__main,
.specpopup__text {
    line-height: 150%;
}
