.special {
  position: relative;
  overflow: hidden;
  padding-top: 122px;
  padding-bottom: 59px;
  background-color: $color-dark;

  @include md {
    padding-top: 89px;
    padding-bottom: 68px;
  }

  @include sm {
    padding-top: 60px;
    padding-bottom: 40px;
  }
}

.special__apply {
  width: 205px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 18px 24px;
  border-radius: 50%;
  border: 1.7px solid $color_light;
  appearance: none;
  outline: none;
  background-color: transparent;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  color: $color_light;
  cursor: pointer;

  @include md {
    width: 192px;
    height: 60px;
    font-size: 18px;
  }

  @include sm {
    width: 176px;
    height: 55px;
    font-size: 16px;
  }
}

.special__container {
  max-width: 1432px;
  margin: 0 auto;
  padding: 0 20px;
  color: $color_light;
}

.special__title {
  max-width: 731px;
  margin: 0 auto 17px;

  @include md {
    margin-bottom: 12px;
  }

  @include sm {
    margin-bottom: 3px;
  }
}

.special__text {
  max-width: 919px;
  margin: 0 auto 80px;
  font-family: $mon;
  font-weight: 400;
  font-size: 24px;
  line-height: 132%;
  text-align: center;
  color: $color_light;

  @include lg {
    font-size: 20px;
  }

  @include md {
    margin-bottom: 28px;
    padding: 0 32px;
    font-size: 18px;
  }

  @include sm {
    font-size: 16px;
  }

  @include xs {
    padding: 0 14px;
  }
}

.special__tab {
  list-style-type: none;
  margin-bottom: 48px;

  @include md {
    margin-bottom: 36px;
  }

  @include sm {
    margin-bottom: 28px;
  }
}

.special__item {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  opacity: 0.4;

  &:hover {
    opacity: 1;
  }

  &:not(:last-child) {
    margin-bottom: 6px;
  }
}

.special__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.special__info {
  display: flex;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  text-decoration: none;
  color: $color_light;
  transition: all 0.2s linear;
  text-align: center;

  p {
    margin-right: 3px;
  }

  @include md {
    font-size: 40px;
  }

  @include sm {
    font-size: 30px;
  }

  @include xs {
    display: inline-block;
    max-width: 360px;
    font-size: 28px;

    p {
      margin-right: 0;
    }
  }
}

.special__icon {
  & img {
    display: block;
    width: 100%;
    height: auto;
  }

  @include md {
    width: 15px;
  }

  @include sm {
    width: 10px;
  }
}
