.judges {
  position: relative;
  padding: 180px 0 130px;
  overflow: hidden;

  @include lg {
    padding: 180px 0 80px;
  }

  @include md {
    padding: 100px 0 80px;
  }

  @include sm {
    padding: 75px 0 60px;
  }
}

.judges__disk {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -63%);
  width: 100%;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    display: block;
    width: 100%;
    height: auto;
  }

  &::after {
    content: '';
    display: block;
    padding-top: 100%;
  }
}

.judges__num {
  position: relative;
  z-index: 5;
}

.judges__title {
  position: relative;
  z-index: 5;
  margin-bottom: 80px;
  color: $color_light;

  @include md {
    margin-bottom: 40px;
  }
}

.judges__descr {
  max-width: 1053px;
  margin: 0 auto 12px;
  font-family: $arina;
  font-weight: 400;
  font-size: 58px;
  line-height: 128%;
  text-transform: uppercase;
  color: $color_light;
  text-align: center;

  @include md {
    max-width: 585px;
    font-size: 30px;
  }

  @include sm {
    max-width: 265px;
    margin-bottom: 10px;
    font-size: 24px;
  }
}

.judges__text {
  max-width: 842px;
  margin: 0 auto 80px;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  text-align: center;
  color: $color_light;

  @include md {
    max-width: 585px;
    margin-bottom: 40px;
    font-size: 16px;
  }

  @include sm {
    max-width: 322px;
    margin-bottom: 24px;
    font-size: 14px;
  }
}

.judges__cross {
  margin: 0 auto 80px;

  @include lg {
    margin-bottom: 40px;
  }

  @include sm {
    margin-bottom: 24px;
  }
}

.judges__slider {
  width: 100%;
  overflow: hidden;
}

.judges__wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 80px 21px;

  @include md {
    gap: 45px 15px;
  }

  @include sm {
    grid-template-columns: repeat(2, 1fr);
    gap: 40px 8px;
  }
}

.judges__item {
  position: relative;
  margin-bottom: 25px;
  overflow: hidden;

  @include md {
    margin-bottom: 15px;
  }

  @include sm {
    margin-bottom: 10px;
  }

  &:after {
    content: '';
    display: block;
    padding-top: 61%;
  }
}

.judges__item-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.judges__image-wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
  width: 100%;
}

.judges__image-disk {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 157.25%;
  height: 252.61%;
  border-radius: 50%;
  overflow: hidden;
}

.judges__shadow {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 100;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: inset 0px 0px 82px rgba(255, 255, 255, 0.82);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;

  @include sm {
    display: none;
  }
}

.judges__info {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 28px 61px 0;
  box-shadow: inset 0px 0px 108px $color_white;
  background-color: $color_purple;
  text-align: center;
  font-family: $mon;
  font-weight: 400;
  font-size: 20px;
  line-height: 136%;
  color: $color_dark;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;

  @include xl {
    font-size: 18px;
  }

  @media (max-width: 1400px) {
    font-size: 14px;
    padding: 32px 25px 0;
  }

  @include md {
    font-size: 12px;
  }

  @include xs {
    display: none;
  }
}

.judges__item:hover .judges__info,
.judges__item:hover .judges__shadow {
  opacity: 1;
  visibility: visible;
}

.judges__img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 63.59%;
  height: 39.59%;

  & img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include xl {
    height: 37.2%;
  }
}

.judges__name,
.judges__function {
  font-family: $mon;
  font-weight: 400;
  font-size: 23px;
  line-height: 128%;
  text-align: center;
  text-transform: uppercase;
  color: $color_light;

  @include md {
    font-size: 14px;
  }
}
